export default {
	props: {
		src: String,
		mode: String,
		lazyLoad: Boolean,
		size: {
			type: Number,
			default: 80
		},
		shape: {
			type: String,
			default: 'circle'
		}
	},

	computed: {
		isShape() {
			return `cl-avatar--${this.shape}`;
		},

		height() {
			return this.size + 'rpx';
		},

		width() {
			return this.size + 'rpx';
		},

		fontSize() {
			return this.size / 1.7 + 'rpx';
		}
	},

	methods: {
		handleLoad(e) {
			this.$emit('error', e);
		},

		handleError(e) {
			this.$emit('load', e);
		}
	}
};
